import Rater from "react-rater"
import { useNavigate, useSearchParams } from "react-router-dom"
import HorizontalLayout from "../../layouts/HorizontalLayout"
import 'react-rater/lib/react-rater.css'
import { useEffect, useState } from "react"
import FeedbackApi from "../../api/feedback"
import Swal from "sweetalert2"
// import './style.scss'

const Feedback = () => {
    const navigate = useNavigate()
    const [searchParams, setSearchParams] = useSearchParams()
    // const [attemptId, setAttemptId] = useState(0)
    const [fbText, setFbText] = useState("")
    const [feedback, setFeedback] = useState({
        overall: 0,
        quality: 0,
        difficulty: 0,
        relevancy: 0,
        ui: 0,
        ux: 0
    })


    const handleSubmit = () => {
        feedback.text = document.getElementById('feedback_text').innerHTML
        if (feedback.overall === 0 || feedback.quality === 0 || feedback.difficulty === 0 || feedback.relevancy === 0 || feedback.ui === 0 || feedback.ux === 0) {
            Swal.fire('Oops...', 'Mohon lengkapi semua yaa', 'info')
        } else {
            FeedbackApi.submitFeedback({
                overall_score: feedback.overall,
                quality_score: feedback.quality,
                difficulty_score: feedback.difficulty,
                relevancy_score: feedback.relevancy,
                ui_score: feedback.ui,
                ux_score: feedback.ux,
                text: fbText,
                attempt_id: searchParams.get('attemptid')
            }).then((res) => {
                if (res.data.ok) {
                    Swal.fire('Berhasil', 'Terima kasih atas feedbacknya :)', 'success').then(() => {
                        navigate(`/result/${searchParams.get('attemptid')}`)
                    })
                } else {
                    Swal.fire('Oops...', res.data.message, 'error')
                }
            }).catch((err) => {
                Swal.fire('Oops...', err.message, 'error')
            })
        }
    }

    const saveRate = (id, rate) => {
        setFeedback((prevState) => ({ ...prevState, [id]: rate }))
    }

    const handleText = (event) => {
        setFbText(event.target.value);
        // const fb = { ...feedback }
        // fb.text = event.target.value
        // setFeedback(fb);
        // console.log(event.target.value)
        // feedback.text = 
        // setFeedback((prevState) => ({ ...prevState, text: event.target.value }))

    }

    useEffect(() => {
        // setAttemptId(searchParams.get('attemptid'))
    }, []);

    return (
        <HorizontalLayout>
            {/* idnya {searchParams.get('attemptid')} */}
            <div className="card">
                <div className="card-header"><h5>Isi Feedback mengenai Tryout SKD Part 1 yang telah kamu kerjakan yaa</h5></div>
                <div className="card-body">

                    <div>
                        <label>Overall Score dari tryout kali ini</label>
                        <div className="fs-1">
                            <Rater total={5} value={1} onRate={(rating) => saveRate('overall', rating.rating)} />
                        </div>
                    </div>

                    <div>
                        <label>Tingkat kesulitan menurut kamu</label>
                        <div className="fs-1">
                            <Rater total={5} value={1} onRate={(rating) => saveRate('difficulty', rating.rating)} />
                        </div>
                    </div>

                    <div>
                        <label>Nilai terhadap kualitas soal</label>
                        <div className="fs-1">
                            <Rater total={5} value={1} onRate={(rating) => saveRate('quality', rating.rating)} />
                        </div>
                    </div>

                    <div>
                        <label>Nilai terhadap relevansi soal</label>
                        <div className="fs-1">
                            <Rater total={5} value={1} onRate={(rating) => saveRate('relevancy', rating.rating)} />
                        </div>
                    </div>

                    <div>
                        <label>Kemudahan penggunaan aplikasi</label>
                        <div className="fs-1">
                            <Rater total={5} value={1} onRate={(rating) => saveRate('ux', rating.rating)} />
                        </div>
                    </div>

                    <div>
                        <label>Nilai terhadap tampilan aplikasi</label>
                        <div className="fs-1">
                            <Rater total={5} value={1} onRate={(rating) => saveRate('ui', rating.rating)} />
                        </div>
                    </div>

                    <div className="mt-3">
                        <label>Masukan atau kritik untuk tryout selanjutnya, curhat juga boleh</label>
                        <textarea className="form-control" name="" id="feedback_text" cols="30" rows="5" onChange={handleText}></textarea>
                    </div>
                </div>
                <div className="card-footer">
                    <button className="btn btn-primary" onClick={handleSubmit}>Kirim Feedback</button>
                </div>
            </div>
        </HorizontalLayout>
    )
}

export default Feedback