import logo from "../../assets/images/logo.png"
import { useGoogleLogin } from '@react-oauth/google';
import AuthLib from "../../api/auth";
import "./login.css"
import Swal from "sweetalert2";
import { setAccessToken } from "../../utils/auth";
import { Link, useNavigate } from "react-router-dom";
import { useState } from "react";
import Auth from "../../api/auth";


const Login = () => {
    const navigate = useNavigate();
    const [email, setEmail] = useState("")
    const [passwd, setPasswd] = useState("")

    const loginGoogle = useGoogleLogin({
        onSuccess: tokenResponse => handleLoginWithGoogle(tokenResponse.access_token)
    });

    const handleLoginWithGoogle = (access_token) => {
        AuthLib.loginWithGoogle(access_token).then((res) => {
            if (res.data.ok) {
                setAccessToken(res.data.data.access_token)
                navigate("/home");
            } else {
                Swal.fire('Oops...', res.data.message, 'error');
            }
        }).catch((err) => {
            Swal.fire('Oops...', err.message, 'error');
        })
    }

    const handleLoginOrdinary = () => {
        Auth.loginWithPassword(email, passwd).then((res) => {
            if (res.data.ok) {
                setAccessToken(res.data.data.access_token)
                navigate("/home");
            } else {
                Swal.fire('Oops...', res.data.message, 'error');
            }
        }).catch((err) => {
            Swal.fire('Oops...', err.message, 'error');
        })
    }

    return (
        <div id="auth">
            <div className="row h-100">
                <div className="col-lg-5 col-12">
                    <div id="auth-left">
                        <div className="">
                            <Link to="/home">
                                <img src={logo} alt="Logo" className="login-logo" />
                            </Link>
                        </div>
                        <h1 className="">Masuk.</h1>
                        <p className="mb-5">Log in with your data that you entered during registration.</p>

                        <form action="index.html">
                            <div className="form-group position-relative has-icon-left mb-4">
                                <input type="email" className="form-control" placeholder="Email" onChange={(e) => setEmail(e.target.value)} />
                                <div className="form-control-icon">
                                    <i className="bi bi-person"></i>
                                </div>
                            </div>
                            <div className="form-group position-relative has-icon-left mb-4">
                                <input type="password" className="form-control" placeholder="Password" onChange={(e) => setPasswd(e.target.value)} />
                                <div className="form-control-icon">
                                    <i className="bi bi-shield-lock"></i>
                                </div>
                            </div>
                            <button type="button" className="btn btn-primary btn-block shadow mt-1 mb-2" onClick={handleLoginOrdinary}>Login</button>
                        </form>
                        <p className="mt-2 text-center">- ATAU -</p>

                        <button className="btn btn-primary btn-block shadow mt-2" onClick={() => loginGoogle()}>Login with Google</button>

                    </div>
                </div>
                <div className="col-lg-7 d-none d-lg-block">
                    <div id="auth-right">

                    </div>
                </div>
            </div>

        </div>
    )
}

export default Login;