import { useEffect, useState } from "react"
import { useNavigate, useParams } from "react-router-dom"
import Swal from "sweetalert2"
import Tryout from "../../api/tryout"
import HorizontalLayout from "../../layouts/HorizontalLayout"

const Result = () => {
    const navigate = useNavigate()
    const { attemptId } = useParams()
    const [attempt, setAttempt] = useState({
        id: 1,
        title: "Nama Tryout",
        attemptId: 21,
        min_twk: "10",
        min_tiu: 10,
        min_tkp: 10,
        score_twk: 0,
        score_tiu: 0,
        score_tkp: 0,
        total_score: 0,
        feedback: false
    })

    const handleFillFeedback = () => {
        console.log("feedback")
        navigate(`/feedback?attemptid=${attemptId}`)
    }

    useEffect(() => {
        Tryout.getAttempt(attemptId).then((res) => {
            if (res.data.ok) {
                setAttempt(res.data.data);
            } else {
                Swal.fire('Oops...', res.data.message, 'error')
            }
        }).catch((err) => {
            Swal.fire('Oops...', err.message, 'error')
        })
    }, [])

    return (
        <HorizontalLayout>

            <div className="row">
                <div className="col-12">
                    <div className="card">
                        <div className="card-header">
                            <h5>Hasil {attempt.title}</h5>
                        </div>


                        <div className="card-body">

                            {
                                (!attempt.feedback) ? (
                                    <div>
                                        <p>Ups, kamu belum isi feedback ya?? tolong diisi dulu ya, setelah itu nilaimu akan keluar</p>
                                        <button className="btn btn-warning" onClick={handleFillFeedback}>Isi Feedback</button>
                                    </div>
                                ) : (
                                    <div className="row justrify-content-center">
                                        <div className="col-6">
                                            <div className="table-responsive text-center">
                                                <table className="table table-lg">
                                                    <thead>
                                                        <th>#</th>
                                                        <th>Subtes</th>
                                                        <th>PG</th>
                                                        <th>Nilai Kamu</th>
                                                        <th></th>
                                                    </thead>
                                                    <tbody>
                                                        <tr>
                                                            <td>1</td>
                                                            <td className="text-bold-500">TWK</td>
                                                            <td>{attempt.min_twk}</td>
                                                            <td>{attempt.score_twk}</td>
                                                            <td>
                                                                {
                                                                    (attempt.min_twk > attempt.score_twk) ? (
                                                                        <span className="badge bg-danger">Gagal</span>
                                                                    ) : (
                                                                        <span className="badge bg-success">Lulus</span>
                                                                    )
                                                                }
                                                            </td>
                                                        </tr>
                                                        <tr>
                                                            <td>2</td>
                                                            <td>TIU</td>
                                                            <td>{attempt.min_tiu}</td>
                                                            <td>{attempt.score_tiu}</td>
                                                            <td>
                                                                {
                                                                    (attempt.min_tiu > attempt.score_tiu) ? (
                                                                        <span className="badge bg-danger">Gagal</span>
                                                                    ) : (
                                                                        <span className="badge bg-success">Lulus</span>
                                                                    )
                                                                }
                                                            </td>
                                                        </tr>
                                                        <tr>
                                                            <td>2</td>
                                                            <td>TKP</td>
                                                            <td>{attempt.min_tkp}</td>
                                                            <td>{attempt.score_tkp}</td>
                                                            <td>
                                                                {
                                                                    (attempt.min_tkp > attempt.score_tkp) ? (
                                                                        <span className="badge bg-danger">Gagal</span>
                                                                    ) : (
                                                                        <span className="badge bg-success">Lulus</span>
                                                                    )
                                                                }
                                                            </td>
                                                        </tr>
                                                    </tbody>
                                                </table>
                                            </div>
                                        </div>
                                    </div>
                                )
                            }


                            {/* <button className="btn btn-primary">
                                <i className="bi bi-download"> </i>
                                Download Pembahasan
                            </button> */}
                        </div>



                    </div>

                </div>
            </div>
        </HorizontalLayout>
    )
}

export default Result