import service from "../utils/request";

export default class Feedback {
    static baseGroupURL = '/feedback';
    static async getFeedbackInfo(attemptId) {
        const res = await service.get(`${this.baseGroupURL}?attempt_id=${attemptId}`);
        return res;
    }

    static async submitFeedback(data) {
        const res = await service.post(`${this.baseGroupURL}/`, data);
        return res;
    }
}