import { useEffect, useState } from "react"
import { useNavigate, useParams } from "react-router-dom"
import Swal from "sweetalert2"
import Tryout from "../../api/tryout"
import AttemptLayout from "../../layouts/AttemptLayout"
import "./style.css"

const Attempt = () => {
    const navigate = useNavigate()
    const { attemptId } = useParams()
    const [current, setCurrent] = useState(0)
    const [remaining, setRemaining] = useState(10);
    const parse = require('html-react-parser');


    const [questions, setQuestions] = useState([
        { id: 21, text: "ini contoh soal nomor 1", a: "a bnar", b: "b benar", c: "c benar", d: "d benar", e: "e benar" },
        { id: 22, text: "ini contoh soal nomor 2", a: "a bnar", b: "b benar", c: "c benar", d: "d benar", e: "e benar" },
        { id: 23, text: "ini contoh soal nomor 2", a: "a bnar", b: "b benar", c: "c benar", d: "d benar", e: "e benar" },
    ])

    useEffect(() => {
        Tryout.getAnswers().then((res) => {
            console.log(res);
            if (res.data.ok) {
                // console.log(res)
                setQuestions(res.data.data.questions)
                setRemaining(res.data.data.remaining)
            } else {
                Swal.fire('Oops...', res.data.message, 'error');
            }
        }).catch((err) => [
            Swal.fire('Oops...', err.message, 'error')
        ])
    }, [])

    // useEffect(() => {
    //     // console.log(current)
    // }, [current]);

    const saveAnswer = (i, ans) => {
        // const anss = { ...questions };
        questions[i].selected = ans.toString()
        Tryout.putAnswer([{ id: questions[i].id, selected_id: ans }]).then((res) => {
            console.log(res)
        }).catch((err) => {
            console.error(err)
        })
        // setQuestions(anss)
    }

    // useEffect(() => {
    //     console.log("jawaban berubah")
    // }, [answers])


    useEffect(() => {
        // exit early when we reach 0
        if (remaining <= 0) {
            Swal.fire({
                icon: 'info',
                text: 'Waktu pengerjaan telah habis',
                title: 'Waktu Habis',
                allowOutsideClick: false
            }).then(() => {
                navigate(`/result/${attemptId}`)
            })
        } else {
            const intervalId = setInterval(() => {
                setRemaining(remaining - 1);
            }, 1000);

            // clear interval on re-render to avoid memory leaks
            return () => clearInterval(intervalId);
            // add timeLeft as a dependency to re-rerun the effect
            // when we update it
        }

    }, [remaining]);

    const finishAttempt = () => {
        Swal.fire({
            title: 'Are you sure?',
            icon: 'warning',
            showCancelButton: true,
            confirmButtonColor: '#3085d6',
            cancelButtonColor: '#d33',
            confirmButtonText: 'Ya, Akhiri Pengerjaan!',
            cancelButtonText: 'Batal'
        }).then((res) => {
            if (res.isConfirmed) {
                Tryout.finishAttempt(attemptId).then((res) => {
                    if (res.data.ok) {
                        Swal.fire('Success', 'Data berhasil disimpan', 'success').then(() => {
                            navigate(`/result/${attemptId}`)
                        })
                    } else {
                        Swal.fire('Oops...', res.data.message, 'error')
                    }
                }).catch((err) => {
                    Swal.fire('Oops...', err.message, 'error')
                })
            }
        })
    }

    return (
        <AttemptLayout>
            <div className="row">
                <div className="col-lg-4">
                    <div className="card">
                        <div className="card-body">
                            <h5 className="text-center">Tryout SKD Part 1</h5>
                            <div className="text-center">
                                <span className="btn btn-primary fw-bold">{Math.floor(remaining / 60)} : {remaining % 60}</span>
                            </div>
                            <div className="text-center">
                                <button className="btn btn-danger mt-2 btn-sm" onClick={finishAttempt}>Akhiri Pengerjaan</button>
                            </div>
                        </div>
                    </div>
                    <div className="card">
                        <div className="card-body">
                            <h5>Navigasi Soal</h5>
                            <div className="">

                                {questions.map((v, i) => {
                                    var cls = (i === current) ? "btn-light" : ((questions[i].selected !== "0") ? "btn-primary" : "btn-secondary")
                                    return (
                                        <button key={i} className={`btn ${cls} nav-q m-1`} onClick={() => setCurrent(i)}>{(i + 1)}</button>
                                    )
                                })}

                                {/* <button className="btn btn-primary nav-q m-1">2</button> */}

                            </div>
                        </div>
                    </div>
                </div>
                <div className="col-lg-8">
                    <div className="card">
                        <div className="card-body">
                            <h5>Soal ke {(current + 1)}</h5>
                            <div>
                                {parse(questions[current].text)}
                            </div>
                            <br className="m-5" />
                            <div className="d-grid gap-2">

                                <button className={(questions[current].selected === "1") ? 'btn btn-primary text-start' : 'btn btn-outline-primary text-start'} onClick={() => saveAnswer(current, 1)}> {parse(questions[current].a)} </button>
                                <button className={(questions[current].selected === "2") ? 'btn btn-primary text-start' : 'btn btn-outline-primary text-start'} onClick={() => saveAnswer(current, 2)}> {parse(questions[current].b)} </button>
                                <button className={(questions[current].selected === "3") ? 'btn btn-primary text-start' : 'btn btn-outline-primary text-start'} onClick={() => saveAnswer(current, 3)}> {parse(questions[current].c)} </button>
                                <button className={(questions[current].selected === "4") ? 'btn btn-primary text-start' : 'btn btn-outline-primary text-start'} onClick={() => saveAnswer(current, 4)}> {parse(questions[current].d)} </button>
                                <button className={(questions[current].selected === "5") ? 'btn btn-primary text-start' : 'btn btn-outline-primary text-start'} onClick={() => saveAnswer(current, 5)}> {parse(questions[current].e)} </button>

                            </div>
                            <div className="mt-3">
                                <button className="btn btn-info m-3" disabled={(current === 0) ? true : false} onClick={() => setCurrent(current - 1)}>Sebelumnya</button>
                                <button className="btn btn-info m-3" disabled={(current === (questions.length - 1)) ? true : false} onClick={() => setCurrent(current + 1)}>Selanjutnya</button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

        </AttemptLayout>
    )
}

export default Attempt;