// CSS
import './assets/css/bootstrap.css';
import './assets/css/fonts.css';
import './assets/css/new.css';

// LIBRARIES
import { BrowserRouter, HashRouter, Navigate, Route, Routes } from 'react-router-dom';
import { GoogleOAuthProvider } from '@react-oauth/google';

// PAGES
import Login from './pages/Login';
import Home from './pages/Home';
import Attempt from './pages/Attempt';
import Preview from './pages/Preview';
import Result from './pages/Result';
import Feedback from './pages/Feedback';
import Profile from './pages/Profile';
import History from './pages/History';

// UTILS
import { getAccessToken } from './utils/auth';

function App() {
  return (
    <GoogleOAuthProvider clientId={process.env.REACT_APP_GOOGLE_CLIENT_ID}>
      <HashRouter>
        <Routes>
          <Route path='home' element={<RequireAuth><Home /></RequireAuth>} />
          <Route path='history' element={<RequireAuth><History /></RequireAuth>} />
          <Route path='profile' element={<RequireAuth><Profile /></RequireAuth>} />
          <Route path='preview/:tryoutId' element={<RequireAuth><Preview /></RequireAuth>} />
          <Route path='attempt/:attemptId' element={<RequireAuth><Attempt /></RequireAuth>} />
          <Route path='result/:attemptId' element={<RequireAuth><Result /></RequireAuth>} />
          <Route path='feedback' element={<RequireAuth><Feedback /></RequireAuth>} />
          <Route exact path='/' element={<Login />} />
        </Routes>
      </HashRouter>
    </GoogleOAuthProvider>
  );
}

function RequireAuth({ children }) {
  return (getAccessToken() !== null) ? children : <Navigate to="/" />;
}

export default App;
