import { getAnswerToken } from "../utils/answer";
import service from "../utils/request";

export default class Tryout {
    static baseGroupURL = '/tryouts';

    static async getAll() {
        const res = await service.get(`${this.baseGroupURL}`);
        return res;
    }

    /**
     * Untuk mendapatkan daftar tryout yang tersedia untuk dikerjakan
     */
    static async getAttempts() {
        const res = await service.get(`${this.baseGroupURL}/attempts`);
        return res;
    }

    /**
     * Untuk mendapatkan informasi mengenai detail tryout
     */
    static async getDetail(tryout_id) {
        const res = await service.get(`${this.baseGroupURL}/${tryout_id}`);
        return res;
    }

    /**
     * Untuk melihat daftar subtest yang ada
     */
    static async getSubtests(tryout_id) {
        const res = await service.get(`${this.baseGroupURL}/subtests?tryout=${tryout_id}`);
        return res;
    }

    /**
     * Memilih tryout yang akan dikerjakan dan mulai pengerjaan
     */
    static async startAttempt(tryout_id) {
        const res = await service.post(`/attempts`, {
            exam: tryout_id
        });
        return res;
    }

    /**
     * Mendapatkan informasi mengenai pengerjaan tryout (global)
     */
    static async getAttempt(attempt_id) {
        const res = await service.get(`/attempts/${attempt_id}`);
        return res;
    }

    /**
     * Untuk mendapatkan soal dan pilihan jawaban
     */
    static async getQuestions(attempt_id) {
        const res = await service.get(`${this.baseGroupURL}/questions?attempt_id=${attempt_id}`, {
            answer_token: getAnswerToken()
        });
        return res;
    }

    /**
     * Untuk mendapatkan soal, pilihan jawaban dan jawaban yang telah dipilih
     */
    static async getAnswers() {
        const res = await service.get(`/answers?answer_token=${getAnswerToken()}`);
        return res;
    }

    /**
     * Untuk menyimpan jawaban
     */
    static async putAnswer(answers) {
        const res = await service.post(`/answers`, {
            answer_token: getAnswerToken(),
            answers: answers
        });
        return res;
    }

    /**
     * Finish
     */
    static async finishAttempt(attempt_id) {
        const res = await service.post(`/attempts/finish/${attempt_id}`);
        return res;
    }

    /**
     * Detail
     */
    static async getResult(attempt_id) {
        const res = await service.get(`${this.baseGroupURL}/attempts/result?attempt_id=${attempt_id}`);
        return res;

    }
}