import { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import TryoutCard from "../../components/TryoutCard";
import HorizontalLayout from "../../layouts/HorizontalLayout"
import Tryout from "../../api/tryout";
import Swal from "sweetalert2";

const Home = () => {
    const navigate = useNavigate()

    const [tryouts, setTryouts] = useState([]);

    const handleSelectTryout = (tryoutId, isFinished) => {
        if (!isFinished) {
            navigate(`/preview/${tryoutId}`)
        } else {
            navigate(`/result/${isFinished}`)
        }
    }

    useEffect(() => {
        Tryout.getAll().then((res) => {
            setTryouts(res.data.data)
        }).catch((err) => {
            Swal.fire({
                title: "Oops...",
                text: err.data.error
            })
        })
    }, [])

    return (
        <HorizontalLayout>
            <div className="page-heading">
                <h3>Selamat datang</h3>
            </div>
            <div className="page-content">
                <section className="row">

                    {
                        tryouts.map((tryout, i) => (
                            <div key={i} className="col-lg-4 col-md-6 col-sm-12">
                                <TryoutCard
                                    title={tryout.title}
                                    description={tryout.description}
                                    image={tryout.image}
                                    onClick={() => handleSelectTryout(tryout.id, tryout.is_finished)}
                                />
                            </div>
                        ))
                    }

                </section>
            </div>
        </HorizontalLayout>
    )
}

export default Home;