import { useState } from "react";
import { useNavigate, useParams } from "react-router-dom";
import Swal from "sweetalert2";
import Tryout from "../../api/tryout";
import HorizontalLayout from "../../layouts/HorizontalLayout";
import { setAnswerToken } from "../../utils/answer";

const Preview = () => {
    const navigate = useNavigate()
    const { tryoutId } = useParams()
    const [tryout, setTryout] = useState({
        title: "Judul Tryout",
        description: "deskripsi tentang tryout",
        attempt_start_at: "27 Mei 2022, 00:00:01",
        attempt_ended_at: "28 Mei 2022, 23:59:59",
        attempt_duration: 100
    })

    useState(() => {
        Tryout.getDetail(tryoutId).then((res) => {
            if (res.data.ok) {
                setTryout(res.data.data)
            } else {
                Swal.fire('Oops...', res.data.message, 'error')
            }
        }).catch((err) => {
            Swal.fire('Oops...', err.message, 'error')
            console.error(err)
        })
        // console.log(tryoutId)
    }, []);

    const handleStartAttempt = () => {

        Swal.fire({
            icon: "question",
            title: "Apakah kamu yakin mengerjakan tryout sekarang?",
            text: "Waktu pengerjaan akan dihitung mundur",
            showCancelButton: true,
            cancelButtonText: "Batal",
            confirmButtonText: "Ya, mulai"
        }).then((res) => {
            if (res.isConfirmed) {
                // Kirim data ke server
                Tryout.startAttempt(tryoutId).then((response) => {
                    if (response.data.ok) {
                        // console.log(response)
                        setAnswerToken(response.data.data.answer_token);
                        navigate(`/attempt/${response.data.data.attempt.id}`)
                    } else {
                        Swal.fire({
                            title: "Oops...",
                            text: response.data.message,
                            icon: 'error'
                        })
                    }
                }).catch((err) => {
                    console.log(err)
                    Swal.fire({
                        title: "Oops...",
                        text: "Terjadi kesalahan",
                        icon: "error"
                    })
                })
            }
        })
    }

    const handleCancel = () => {
        navigate("/home")
    }

    return (
        <HorizontalLayout>
            <div className="card">
                <div className="card-header">
                    <h3>{tryout.title}</h3>
                </div>
                <div className="card-body">
                    <p>{tryout.description}</p>

                    <p>Durasi pengerjaan : {tryout.attempt_duration} menit</p>
                    <p>Mulai pengerjaan : {tryout.attempt_open_at}</p>
                    <p>Batas akhir waktu pengerjaan : {tryout.attempt_closed_at}</p>


                    Peraturan:
                    <ol>
                        <li>Peserta dihimbau tidak bekerja sama dalam mengerjakan soal tryout.</li>
                        <li>Kerjakan sebaik yang kalian bisa</li>
                        <li>Waktu pengerjaan akan dimulai ketika peserta meng-klik tombol "Mulai Pengerjaan" dibawah</li>
                        <li>Apabila terdapat kesalahan sistem atau hal yang tidak diinginkan, harap langsung dm Instagram agar bisa segera ditangai. Waktu pengerjaan akan dikembalikan seperti semula.</li>
                    </ol>
                </div>
                <div className="card-footer">
                    <button className="btn btn-primary m-2" onClick={handleStartAttempt}>Mulai Pengerjaan</button>
                    <button className="btn btn-secondary m-2" onClick={handleCancel}>Batal</button>
                </div>
            </div>
        </HorizontalLayout>
    )
}

export default Preview;